*{
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

header{
  border-bottom: 0.5px solid lightgray;
}
img {
  width: 20%;
}

nav{
  margin-top: 1rem;
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin-bottom: 1rem;
}

ul {
  display: flex;
  justify-content: space-between;
  width: 50%;
  margin-right: 2rem;
}
ul li {
  list-style: none;
  cursor: pointer;
}
 ul li:hover {
font-weight: bold;
 }

 section {
  text-align: center;
  margin: 4rem 0;
 }
 .box {
  display: flex;
  justify-content: space-evenly;
  margin: 3rem 0;
  align-items: center;
 }

 .box2 {
  margin: 3rem 0;
 }
 .box2 p {
  margin-bottom: 2rem;
 }

 .box2 img {
  width: 100%;
 }

 footer{
  display: flex;
  justify-content: space-between;
  border-top: 0.5px solid lightgray;
 }
 footer div {
  margin: 1rem 3rem;
 }

 footer div p, footer div li {
  font-size: small;
  margin-top: 0.5rem;
 }

 .list {
  display: block;
 }

 .box3{
  text-align: left;
  align-self: right;
 }

